/* === REPORT SECTIONS COMPONENT === */
/* Section styles for summary, recommendations, and detailed breakdowns */

/* === SUMMARY SECTION (BEM) === */
.summary-section {
    margin-top: 2rem;
    padding: 2rem;
    background: var(--theme-surface-primary, #ffffff);
    border-radius: 0.75rem;
    border: 1px solid var(--theme-border-primary, #e2e8f0);
}

.summary-section__title {
    font-size: 1.25rem;
    font-weight: 700;
    color: var(--theme-text-danger, #ef4444);
    background: var(--theme-surface-danger-subtle, #fee2e2);
    padding: 0.5rem 1rem;
    border-radius: 0.5rem;
    display: inline-block;
    margin-bottom: 0.5rem;
}

.summary-section__subtitle {
    font-size: 1.5rem;
    font-weight: 600;
    color: var(--theme-text-primary, #1e293b);
    margin-bottom: 1rem;
}

.summary-section__summary {
    font-size: 1rem;
    color: var(--theme-text-secondary, #475569);
    margin-bottom: 1.5rem;
    line-height: 1.5;
}

.summary-section__meta-grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1rem;
    margin-bottom: 1.5rem;
    background: var(--theme-surface-secondary, #f8fafc);
    padding: 1rem;
    border-radius: 0.5rem;
}

.summary-section__meta-item {
    background: var(--theme-surface-primary, white);
    padding: 0.75rem;
    border-radius: 0.25rem;
    border: 1px solid var(--theme-border-primary, #e2e8f0);
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.summary-section__meta-label {
    font-weight: 500;
    color: var(--theme-text-primary, #1e293b);
}

.summary-section__meta-value {
    color: var(--theme-text-secondary, #475569);
}

.summary-section__score-container {
    display: flex;
    justify-content: flex-start;
}

.summary-section__score-display {
    background: linear-gradient(135deg, var(--risk-color-light) 0%, var(--risk-color) 100%);
    color: var(--theme-text-on-accent, white);
    border-radius: 0.75rem;
    padding: 1.5rem;
    text-align: center;
    box-shadow: var(--theme-shadow-md, 0 4px 8px rgba(0,0,0,0.1));
    width: 200px;
    height: 200px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.summary-section__score-number {
    font-size: 4rem;
    font-weight: 800;
    line-height: 1;
}

.summary-section__score-total {
    font-size: 1rem;
    opacity: 0.9;
    margin-top: 0.5rem;
}

/* === RECOMMENDATIONS (BEM) === */
.report-recommendations {
    margin-top: 2rem;
    padding: 2rem;
    background: var(--theme-surface-secondary, #f8fafc);
    border-radius: 0.75rem;
    border: 1px solid var(--theme-border-primary, #e2e8f0);
}

.report-recommendations__heading {
    font-size: 1.75rem;
    font-weight: 700;
    color: var(--theme-text-primary, #1e293b);
    margin-bottom: 1.5rem;
    position: relative;
    padding-left: 2.5rem;
}

.report-recommendations__heading::before {
    content: '✨';
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    font-size: 1.5rem;
}

.report-recommendations__list {
    list-style: none;
    padding: 0;
    margin: 0;
}

.report-recommendations__item {
    display: flex;
    align-items: flex-start;
    margin-bottom: 1rem;
    padding-bottom: 1rem;
    border-bottom: 1px dashed var(--theme-border-primary, #e2e8f0);
}

.report-recommendations__item:last-child {
    margin-bottom: 0;
    padding-bottom: 0;
    border-bottom: none;
}

.report-recommendations__bullet {
    flex-shrink: 0;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background: var(--risk-color);
    display: flex;
    align-items: center;
    justify-content: center;
    color: var(--theme-text-on-accent, white);
    font-size: 0.8rem;
    font-weight: 600;
    margin-right: 1rem;
    margin-top: 0.25rem;
}

.report-recommendations__content {
    flex-grow: 1;
}

.report-recommendations__title {
    font-size: 1rem;
    font-weight: 600;
    color: var(--theme-text-primary, #1e293b);
    margin-bottom: 0.5rem;
}

.report-recommendations__text {
    font-size: 0.95rem;
    color: var(--theme-text-secondary, #475569);
    line-height: 1.6;
}

/* === DETAILED BREAKDOWN SECTION (BEM) === */
.detailed-breakdown-section {
    margin-top: 2rem;
    padding: 2rem;
    background: var(--theme-surface-primary, #ffffff);
    border-radius: 0.75rem;
    border: 1px solid var(--theme-border-primary, #e2e8f0);
}

.detailed-breakdown-section__header {
    margin-bottom: 1.5rem;
}

.detailed-breakdown-section__title {
    font-size: 1.75rem;
    font-weight: 700;
    color: var(--theme-text-primary, #1e293b);
    display: flex;
    align-items: center;
    gap: 0.75rem;
}

.detailed-breakdown-section__title svg {
    color: var(--theme-text-danger, #ef4444);
}

.detailed-breakdown-section__categories-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 1.5rem;
}

/* === RECOMMENDATIONS SECTION (BEM) === */
.recommendations-section {
    margin-top: 2rem;
    padding: 2rem;
    background: var(--theme-surface-primary, #ffffff);
    border-radius: 0.75rem;
    border: 1px solid var(--theme-border-primary, #e2e8f0);
}

.recommendations-section__header {
    margin-bottom: 1.5rem;
}

.recommendations-section__title {
    font-size: 1.75rem;
    font-weight: 700;
    color: var(--theme-text-primary, #1e293b);
    display: flex;
    align-items: center;
    gap: 0.75rem;
}

.recommendations-section__title svg {
    color: var(--theme-text-warning, #f59e0b);
}

.recommendations-section__grid {
    list-style: none;
    padding: 0;
    margin: 0;
}

/* Media Queries for Sections */
@media (max-width: 768px) {
    .summary-section__grid {
        grid-template-columns: 1fr;
    }
    
    .detailed-breakdown-section__categories-grid {
        grid-template-columns: 1fr;
    }
}