/* === REPORT HEADER COMPONENT === */
/* Header styles for all report types */

/* === HEADER (BEM) === */
.report-header {
    background: var(--theme-gradient-header, linear-gradient(135deg, #1e40af 0%, #3b82f6 100%));
    color: var(--theme-text-on-accent, white);
    padding: 3rem 2rem;
    position: relative;
    overflow: hidden;
}

.report-header::before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    width: 200px;
    height: 200px;
    background: var(--theme-accent-subtle, rgba(255, 255, 255, 0.1));
    border-radius: 50%;
    transform: translate(50px, -50px);
}

.report-header__content {
    position: relative;
    z-index: 2;
    text-align: center;
}

.report-header__title {
    font-size: 2.5rem;
    font-weight: 800;
    margin-bottom: 0.5rem;
    letter-spacing: -0.025em;
}

.report-header__subtitle {
    font-size: 1.25rem;
    opacity: 0.9;
    font-weight: 300;
    margin-bottom: 1.5rem;
}

.report-header__tool-highlight {
    background: var(--theme-surface-accent, rgba(255, 255, 255, 0.15));
    padding: 1.5rem 2rem;
    border-radius: 12px;
    margin: 2rem auto;
    max-width: 500px;
    text-align: center;
    backdrop-filter: blur(10px);
    border: 1px solid var(--theme-border-accent, rgba(255, 255, 255, 0.2));
}

.report-header__tool-name {
    font-size: 1.75rem;
    font-weight: 700;
    margin-bottom: 0.5rem;
    color: var(--theme-text-on-accent, white);
    text-align: center;
}

.report-header__tool-name--centered {
    text-align: center;
}

.report-header__tool-subtitle {
    font-size: 1rem;
    opacity: 0.9;
    color: var(--theme-text-on-accent-subtle, rgba(255, 255, 255, 0.9));
    text-align: center;
    margin: 0;
}

.report-header__meta {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 2rem;
    padding-top: 2rem;
    border-top: 1px solid var(--theme-border-accent, rgba(255, 255, 255, 0.2));
    font-size: 0.9rem;
    flex-wrap: wrap;
    gap: 30px;
}

.report-header__meta-item {
    display: flex;
    align-items: center;
    gap: 8px;
}